body {
  color: #dd5371;
  background-color: rgba(224, 183, 204, .486);
  margin: 40px 80px;
}

.breed-select {
  font-style: normal;
}

.hidden {
  display: none;
}

.postImage {
  max-width: 400px;
  max-height: 400px;
  margin: 30px auto;
  padding-top: 30px;
}

.loader {
  text-align: center;
  font-style: italic;
}

.loaderIcon {
  width: 48px;
  height: 40px;
  background: #fff;
  border-radius: 15% 15% 35% 35%;
  margin-top: 30px;
  display: inline-block;
  position: relative;
}

.loaderIcon:after {
  content: "";
  box-sizing: border-box;
  width: 16px;
  height: 20px;
  border: 4px solid #fff;
  border-radius: 0 4px 4px 0;
  position: absolute;
  top: 8px;
  left: 45px;
}

.loaderIcon:before {
  content: "";
  width: 1px;
  height: 10px;
  color: #fff;
  box-sizing: border-box;
  animation: animloader 1s infinite;
  position: absolute;
  top: -15px;
  left: 11px;
}

@keyframes animloader {
  0% {
    box-shadow: 2px 0 rgba(255, 255, 255, 0), 12px 0 rgba(255, 255, 255, .3), 20px 0 rgba(255, 255, 255, 0);
  }

  50% {
    box-shadow: 2px -5px rgba(255, 255, 255, .5), 12px -3px rgba(255, 255, 255, .5), 20px -2px rgba(255, 255, 255, .6);
  }

  100% {
    box-shadow: 2px -8px rgba(255, 255, 255, 0), 12px -5px rgba(255, 255, 255, 0), 20px -5px rgba(255, 255, 255, 0);
  }
}

